// index.sass
.mainContainer {
  .pc-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .pc-list-item {
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
      .img {
        @include coverBg; }
      .info {
        margin-top: 7px;
        .info-title {
          @include dTime; }
        .info-txt {
          color: #abb7c4;
          line-height: 1.7em; } } }
    &.man-list {
      .pc-list-item {
        width: 22%;
        @include phone {
          width: 47%; }
        .img {
          @include dTime;
          @include ratio(3,5);
          transform: skew(-4deg); }
        .info {
          .info-title {
            .role {
              font-size: 0.7em;
              margin-left: 1em;
              font-weight: normal;
              color: #bbb; } } }
        &:hover {
          .img {
            transform: skew(-4deg) scale(1.05); } }


        &.director {
          width: 100%;
          flex-direction: row;
          @include phone {
            flex-direction: column; }
          .img {
            width: 50%;
            @include ratio(20,9);
            flex-shrink: 0;
            @include phone {
              width: 100%; } }
          .info {
            padding-left: 50px;
            @include phone {
              padding-left: 15px; }
            .info-title {
              font-size: 24px; }
            .team {
              line-height: 1.7em;
              color: #999;
              font-size: 15px; } } } } }

    &.film-list {
      .pc-list-item {
        width: 48%;
        cursor: pointer;
        @include phone {
          width: 100%;
          flex-direction: row;
          .img {
            width: 35%;
            flex-shrink: 0; } }
        .img {
          opacity: .8;
          @include dTime;
          @include ratio(16,9);
          @include dTime;
          .note {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: rgba(black,.7);
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            .date {
              letter-spacing: 0.3em;
              font-size: 28px; }
            .note-txt {
              text-transform: uppercase;
              margin-top: 15px;
              letter-spacing: 0.15em; } } }

        .info {
          background-color: #071829;
          margin-top: -5px;
          padding: 15px 35px; }
        &[data-vid=''] {
          pointer-events: none; }
        &:not([data-vid='']) {
          .img {
            &:after {
              content: '';
              @include bg('owl.video.play.png');
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              position: absolute;
              @include dTime; }
            .note {
              display: none; } } }
        &:hover {
          .img {
            opacity: 1;
            transform: scale(1.05); }

          .info {
            .info-title {
              color: #4280bf; } } } }

      &.film-list-2 {
        .pc-list-item {
          width: 100%;
          flex-direction: row;
          margin-bottom: 40px;
          @include phone {
            flex-direction: column; }

          .img {
            width: 50%;


            @include phone {
              width: 100%; } }



          .info {
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding-left: 70px;
            background-color: transparent;
            @include phone {
              width: 100%;
              padding-left: 25px;
              background-color: #071829; }


            .info-title {
              font-size: 24px;
              letter-spacing: .15em;
              transform: skew(-7deg); }
            .info-txt {
              margin-top: 0; } } } } }





    &.pro-list {


      a:hover {
        text-decoration: none; }
      .pc-list-item {
        width: 23%;
        cursor: pointe;
        @include dTime;
        @include phone {
          width: 47%; }
        .img {
          @include ratio(40,43);
          @include dTime; }
        .info {
          &.withHand {
            position: relative;
            &:after {
              content: '';
              position: absolute;
              right: 16px;
              top: -10px;
              @include bg('hand.png');
              @include updown; } } }
        &.ptr-n {
          pointer-events: none; }
        &:hover {
          .info {
            .info-title {
              color: #4280bf; } }
          .img {
            // opacity: .85
            transform: scale(1.05); } } } } } }

