// commons
.mainContainer {
  .round-bd {
    border-radius: 5px;
    overflow: hidden; }
  .ls-1 {
    letter-spacing: .15em; }

  .tempHide {
    transform: translate(0, 50px);
    opacity: 0;
    @include dTime(.8s);

    &.left {
      transform: translate(25px, 0px);
      @include phone {
        transform: translate(25px, 0px); } }
    &.right {
      transform: translate(50px, 0px); }
    &.top {
      transform: translate(0px, -50px); }
    &.t-show {
      opacity: 1;
      transform: translate(0, 0); } } }

.font-1 {
  font-family: "Dosis"; }
